import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import storage from '../../utils/storage';
import { signinWithToken } from './authAPI';

const initialState = {
  _token: storage.get('_token'),
  userdata: null,
  status: 'idle',
};

export const checkAuthAsync = createAsyncThunk(
  'auth/checkAuth',
  async (_, { getState, dispatch }) => {
    const res = await signinWithToken();
    return res.data;
  },
);

export const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setToken: (state, action) => {
      state._token = action.payload;
    },
    setUserData: (state, action) => {
      state.userdata = action.payload;
    },
  },
  extraReducers: builder => {
    builder
      .addCase(checkAuthAsync.pending, state => {
        state.status = 'loading';
      })
      .addCase(checkAuthAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        if (action.payload.status !== 'success') {
          state._token = null;
        }
      });
  },
});

export const { setToken, setUserData } = authSlice.actions;

export const selectToken = state => state.auth._token;
export const selectUserdata = state => state.auth.userdata;

// export const thunkByHand = data => (dispatch, getState) => {
//   const _token = selectToken(getState());
//   dispatch(setToken(_token));
// };

export default authSlice.reducer;
